import { Component, OnInit  } from '@angular/core';

import { Router,  NavigationExtras } from '@angular/router';
import { AlertController , ModalController, ActionSheetController ,  MenuController  , NavParams} from '@ionic/angular';
import { AuthenticationService } from '../../services/Authentication.service';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-dispositivos-list',
  templateUrl: './dispositivos-list.page.html',
  styleUrls: ['./dispositivos-list.page.scss'],
})
export class DispositivosListPage implements OnInit {
  tituloxx = '';
  tiposus = '5be1b6479c9f2200e8311570';
  APP = '';
  luser: any = [];
  idempresa = '';
  msg = '';
  seguro: any;
  apifoto: String ;
  efecto = 0;
  items: any  = [] ;
  items2: any  = [] ;
variables: any = [];
trfid: any = [];
idllave: any;

viewType = 'grid';

itemsList: Array<any>;



  constructor(public alertController: AlertController,  public translateService: TranslateService    ,
     public router: Router, public actionSheetController: ActionSheetController ,
    public modalController: ModalController, private navParams: NavParams,
    public menu: MenuController , private authenticationService: AuthenticationService) {
      this.apifoto = this.authenticationService.getapifoto();

     }

     dismissModal() {     this.modalController.dismiss('close');      }

     padLeadingZeros(num, size) {
      let s = num +'';
      while (s.length < size) { s = "0" + s; }
      return s;
  }

  
  ngOnInit() {

    this.tituloxx =  this.navParams.get('title');
    this.luser =   this.navParams.get('luser');
    this.trfid =  this.navParams.get('trfid');
    this.translateService.get('SEGUROM').subscribe((value) => {   this.seguro = value;  });
    this.translateService.get('MSGM').subscribe((value) => {   this.msg = value;  });

      this.idempresa =  this.authenticationService.getempresa();
      if (this.trfid[0].codigo1 !== '') {      this.items2.push({codigo:  this.trfid[0].codigo1, nombre: this.trfid[0].dispositivo1}); }
      if (this.trfid[0].codigo2 !== '') {      this.items2.push({codigo:  this.trfid[0].codigo2, nombre: this.trfid[0].dispositivo2}); }
      if (this.trfid[0].codigo3 !== '') {      this.items2.push({codigo:  this.trfid[0].codigo3, nombre: this.trfid[0].dispositivo3}); }
      if (this.trfid[0].codigo4 !== '') {      this.items2.push({codigo:  this.trfid[0].codigo4, nombre: this.trfid[0].dispositivo4}); }
      if (this.trfid[0].codigo5 !== '') {      this.items2.push({codigo:  this.trfid[0].codigo5, nombre: this.trfid[0].dispositivo5}); }
this.pideregistros('todos');

  }

  pideregistros(op) {
    this.itemsList = [];
   
    this.authenticationService.getReg('todos/' + op + '/' + this.luser[0].idempresa._id + '/' +  this.luser[0]._id, 'tarjetacreditos', 0).then((data) => {
 
    this.items =  data;
    this.itemsList = this.items;
}, (err) => {
this.authenticationService.presentAlert(err.error , this.msg , '');
});
}

}
