import { Component } from '@angular/core';

import { PopoverController } from '@ionic/angular';

@Component({
  template: `
    <ion-list>
     
      <ion-item button (click)="close('Agregar Tarjetas')">
        <ion-label>Agregar Tarjetas</ion-label>
      </ion-item>
      <ion-item button (click)="close('Billing')">
        <ion-label>Billing</ion-label>
      </ion-item>
      <ion-item button (click)="close('Transferencias')">
        <ion-label>Transferencias</ion-label>
      </ion-item>
     
    </ion-list>
  `
})
export class PopoverPage {
  constructor(public popoverCtrl: PopoverController) {}

  support() {
    // this.app.getRootNavs()[0].push('/support');
    this.popoverCtrl.dismiss();
  }

  close(url: string) {
   
    this.popoverCtrl.dismiss({data: url});
  }
}
